
"use client";

import * as React from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { Textarea } from "@/components/ui/textarea";
import { Play, Pause, RefreshCw, Loader2, Video, StopCircle, Save, History, ChevronsRight, Trash2 } from "lucide-react";
import { Slider } from "@/components/ui/slider";
import { cn } from "@/lib/utils";
import { useToast } from "@/hooks/use-toast";
import { generatePracticeTopics, type GeneratePracticeTopicsInput } from "@/ai/flows/generate-practice-topics-flow";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion";
import { format } from "date-fns";

type PracticeMode = "extemp" | "impromptu";
type Topic = { id: number; text: string };

type SavedSpeech = {
    id: string;
    topic: string;
    notes: string;
    prepTime: number;
    speechTime: number;
    mode: PracticeMode;
    videoUrl: string; // Store as blob URL or data URL
    date: string;
};

const SAVED_SPEECHES_STORAGE_KEY = 'work-session-saved-speeches';

export default function ExtempPracticePage() {
    const { toast } = useToast();
    const [mode, setMode] = React.useState<PracticeMode>("extemp");
    const [isLoadingTopics, setIsLoadingTopics] = React.useState(false);
    const [topics, setTopics] = React.useState<Topic[]>([]);
    const [selectedTopic, setSelectedTopic] = React.useState<Topic | null>(null);
    const [notes, setNotes] = React.useState("");

    const [initialPrepTime, setInitialPrepTime] = React.useState(30 * 60);
    const [initialSpeechTime, setInitialSpeechTime] = React.useState(7.5 * 60);
    const [prepTime, setPrepTime] = React.useState(initialPrepTime);
    const [speechTime, setSpeechTime] = React.useState(initialSpeechTime);

    const [prepTimerActive, setPrepTimerActive] = React.useState(false);
    const [speechTimerActive, setSpeechTimerActive] = React.useState(false);

    const prepTimeIntervalRef = React.useRef<NodeJS.Timeout | null>(null);
    const speechTimeIntervalRef = React.useRef<NodeJS.Timeout | null>(null);

    // Recording states
    const [hasCameraPermission, setHasCameraPermission] = React.useState(true);
    const [isRecording, setIsRecording] = React.useState(false);
    const [videoUrl, setVideoUrl] = React.useState<string | null>(null);
    const videoRef = React.useRef<HTMLVideoElement>(null);
    const mediaRecorderRef = React.useRef<MediaRecorder | null>(null);
    const recordedChunksRef = React.useRef<Blob[]>([]);

    // Saved speeches states
    const [savedSpeeches, setSavedSpeeches] = React.useState<SavedSpeech[]>([]);
    const [viewingArchived, setViewingArchived] = React.useState(false);

    // Load saved speeches from local storage
    React.useEffect(() => {
        if (typeof window !== 'undefined') {
            try {
                const storedSpeeches = localStorage.getItem(SAVED_SPEECHES_STORAGE_KEY);
                if (storedSpeeches) {
                    setSavedSpeeches(JSON.parse(storedSpeeches));
                }
            } catch (e) {
                console.error("Failed to load speeches from localStorage", e)
            }
        }
    }, []);

    // Save speeches to local storage
    React.useEffect(() => {
        if (typeof window !== 'undefined') {
            localStorage.setItem(SAVED_SPEECHES_STORAGE_KEY, JSON.stringify(savedSpeeches));
        }
    }, [savedSpeeches]);
    
    // Camera permission and cleanup
    React.useEffect(() => {
        let stream: MediaStream | null = null;
        
        const getCameraPermission = async () => {
          try {
            stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
            setHasCameraPermission(true);
            if (videoRef.current) {
              videoRef.current.srcObject = stream;
            }
          } catch (error) {
            console.error('Error accessing camera:', error);
            setHasCameraPermission(false);
             toast({
                variant: "destructive",
                title: 'Camera Access Denied',
                description: 'Please enable camera permissions in your browser settings to use this feature.',
            });
          }
        };

        getCameraPermission();
        
        return () => {
            if (stream) {
                stream.getTracks().forEach(track => track.stop());
            }
            if (prepTimeIntervalRef.current) clearInterval(prepTimeIntervalRef.current);
            if (speechTimeIntervalRef.current) clearInterval(speechTimeIntervalRef.current);
        };
    }, []);


    const restoreCameraStream = async () => {
        try {
            const stream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
            if (videoRef.current) {
                videoRef.current.srcObject = stream;
            }
            setHasCameraPermission(true);
        } catch (error) {
             console.error('Error re-accessing camera:', error);
             setHasCameraPermission(false);
        }
    };


    React.useEffect(() => {
        if (prepTime <= 0 && prepTimerActive) {
            if (prepTimeIntervalRef.current) clearInterval(prepTimeIntervalRef.current);
            setPrepTimerActive(false);
        }
    }, [prepTime, prepTimerActive]);

    React.useEffect(() => {
        if (speechTime <= 0 && speechTimerActive) {
            if (speechTimeIntervalRef.current) clearInterval(speechTimeIntervalRef.current);
            setSpeechTimerActive(false);
            stopRecording();
        }
    }, [speechTime, speechTimerActive]);

    const handleModeChange = (newMode: string) => {
        setMode(newMode as PracticeMode);
        const newInitialPrep = newMode === 'impromptu' ? 2 * 60 : 30 * 60;
        const newInitialSpeech = newMode === 'impromptu' ? 5.5 * 60 : 7.5 * 60;
        setInitialPrepTime(newInitialPrep);
        setInitialSpeechTime(newInitialSpeech);
        resetPractice(newInitialPrep, newInitialSpeech);
    };

    const handleGenerateTopics = async () => {
        setIsLoadingTopics(true);
        // Don't reset everything, just the topics
        setTopics([]);
        setSelectedTopic(null);

        try {
            const input: GeneratePracticeTopicsInput = { type: mode };
            const result = await generatePracticeTopics(input);
            setTopics(result.topics.map((text, id) => ({ id, text })));
        } catch (error) {
            console.error("Failed to generate topics:", error);
            toast({
                variant: "destructive",
                title: "Error",
                description: "Could not generate topics. Please try again.",
            });
        } finally {
            setIsLoadingTopics(false);
        }
    };
    
    const handleTopicSelect = (topic: Topic) => {
        if(selectedTopic) return;
        setSelectedTopic(topic);
        if (mode === 'extemp') {
            startPrepTimer();
        }
    };

    const resetPractice = (prep: number, speech: number) => {
        setViewingArchived(false);
        setTopics([]);
        setSelectedTopic(null);
        setNotes("");
        
        if (videoUrl) {
            URL.revokeObjectURL(videoUrl);
        }
        setVideoUrl(null);
        recordedChunksRef.current = [];
        setIsRecording(false);
        
        setPrepTimerActive(false);
        setSpeechTimerActive(false);
        if (prepTimeIntervalRef.current) clearInterval(prepTimeIntervalRef.current);
        if (speechTimeIntervalRef.current) clearInterval(speechTimeIntervalRef.current);
        setPrepTime(prep);
        setSpeechTime(speech);

        if (videoRef.current) {
            videoRef.current.muted = true;
            if (!videoRef.current.srcObject) {
                 restoreCameraStream();
            }
        }
    };

    const startRecording = () => {
        if (!videoRef.current?.srcObject || !hasCameraPermission) {
            toast({ variant: "destructive", title: "Camera not available", description: "Please grant camera permission."});
            return;
        }
        if (mediaRecorderRef.current && mediaRecorderRef.current.state === "recording") {
            return;
        }
        
        const stream = videoRef.current.srcObject as MediaStream;
        mediaRecorderRef.current = new MediaRecorder(stream, { mimeType: 'video/webm; codecs=vp9,opus' });
        
        recordedChunksRef.current = [];
        mediaRecorderRef.current.ondataavailable = (event) => {
            if (event.data.size > 0) {
                recordedChunksRef.current.push(event.data);
            }
        };

        mediaRecorderRef.current.onstop = () => {
            const blob = new Blob(recordedChunksRef.current, { type: 'video/webm' });
            const url = URL.createObjectURL(blob);
            setVideoUrl(url);

            const currentStream = videoRef.current?.srcObject as MediaStream;
            if (currentStream) {
                currentStream.getTracks().forEach(track => track.stop());
            }

            if (videoRef.current) {
                videoRef.current.srcObject = null;
                videoRef.current.muted = false;
            }
            setIsRecording(false);
        };
        
        mediaRecorderRef.current.start();
        setIsRecording(true);
        startSpeechTimer();
    };

    const stopRecording = () => {
        if (mediaRecorderRef.current && mediaRecorderRef.current.state === 'recording') {
            mediaRecorderRef.current.stop();
            pauseSpeechTimer();
        }
    };

    const handleSaveSpeech = () => {
        if (!videoUrl || !selectedTopic) {
            toast({ variant: 'destructive', title: 'Cannot Save', description: 'No video or topic available to save.' });
            return;
        }

        const newSavedSpeech: SavedSpeech = {
            id: `speech-${Date.now()}`,
            topic: selectedTopic.text,
            notes,
            prepTime: initialPrepTime - prepTime,
            speechTime: initialSpeechTime - speechTime,
            mode,
            videoUrl,
            date: new Date().toISOString(),
        };

        setSavedSpeeches(prev => [newSavedSpeech, ...prev]);
        toast({ title: 'Speech Saved!', description: 'Your practice session has been archived.' });
        resetPractice(initialPrepTime, initialSpeechTime);
    };

    const handleViewArchivedSpeech = (speech: SavedSpeech) => {
        resetPractice(speech.prepTime, speech.speechTime);
        setViewingArchived(true);
        setSelectedTopic({ id: 0, text: speech.topic });
        setNotes(speech.notes);
        setPrepTime(speech.prepTime);
        setSpeechTime(speech.speechTime);
        setMode(speech.mode);
        setVideoUrl(speech.videoUrl);

        if (videoRef.current && videoRef.current.srcObject) {
            const stream = videoRef.current.srcObject as MediaStream;
            stream.getTracks().forEach(track => track.stop());
            videoRef.current.srcObject = null;
        }

        if (videoRef.current) {
            videoRef.current.muted = false;
        }
    };
    
    const handleDeleteSpeech = (id: string) => {
        setSavedSpeeches(prev => prev.filter(s => s.id !== id));
        toast({ title: 'Speech Deleted', variant: 'destructive'});
    }


    // Timer controls
    const startPrepTimer = () => {
        if (prepTime <= 0) return;
        setPrepTimerActive(true);
        prepTimeIntervalRef.current = setInterval(() => {
            setPrepTime(prev => prev - 1);
        }, 1000);
    };

    const pausePrepTimer = () => {
        setPrepTimerActive(false);
        if (prepTimeIntervalRef.current) clearInterval(prepTimeIntervalRef.current);
    };

    const startSpeechTimer = () => {
        if (speechTime <= 0) return;
        setSpeechTimerActive(true);
        speechTimeIntervalRef.current = setInterval(() => {
            setSpeechTime(prev => prev - 1);
        }, 1000);
    };

    const pauseSpeechTimer = () => {
        setSpeechTimerActive(false);
        if (speechTimeIntervalRef.current) clearInterval(speechTimeIntervalRef.current);
    };
    
    const handleImpromptuSlider = (value: number[]) => {
        const newPrepTime = value[0] * 60;
        setPrepTime(newPrepTime);
        setSpeechTime((7.5 * 60) - newPrepTime);
        setInitialPrepTime(newPrepTime);
        setInitialSpeechTime((7.5 * 60) - newPrepTime);
    };

    const formatTime = (seconds: number) => {
        const mins = Math.floor(seconds / 60);
        const secs = seconds % 60;
        return `${String(mins).padStart(2, '0')}:${String(secs).padStart(2, '0')}`;
    };

    const renderTopic = (topic: Topic) => (
        <Card 
            key={topic.id}
            className={cn(
                "cursor-pointer transition-all hover:bg-primary/10",
                selectedTopic && selectedTopic.id !== topic.id ? "hidden" : "",
                selectedTopic && selectedTopic.id === topic.id ? "bg-primary text-primary-foreground border-primary shadow-lg" : "",
                viewingArchived && "cursor-default"
            )}
            onClick={() => !viewingArchived && handleTopicSelect(topic)}
        >
            <CardContent className="p-4 text-center">
                <p className="font-medium">{topic.text}</p>
            </CardContent>
        </Card>
    );

    const viewedSpeech = viewingArchived ? savedSpeeches.find(s => s.videoUrl === videoUrl) : null;

    return (
        <div className="flex flex-col gap-6">
            <div className="flex justify-between items-start">
                <div>
                    <h1 className="text-3xl font-bold font-headline">Extemp / Impromptu Practice</h1>
                    <p className="text-muted-foreground">Hone your speaking skills with timed practice sessions.</p>
                </div>
                 <Button variant="outline" onClick={() => resetPractice(initialPrepTime, initialSpeechTime)}>
                    <RefreshCw className="mr-2 h-4 w-4" /> New Practice
                </Button>
            </div>


            <Tabs value={mode} onValueChange={handleModeChange} className="w-full">
                <TabsList className="grid w-full grid-cols-2">
                    <TabsTrigger value="extemp" disabled={viewingArchived}>Extemporaneous</TabsTrigger>
                    <TabsTrigger value="impromptu" disabled={viewingArchived}>Impromptu</TabsTrigger>
                </TabsList>
                <TabsContent value="extemp" />
                <TabsContent value="impromptu" />
            </Tabs>

            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                <div className="lg:col-span-2 space-y-6">
                    {!viewingArchived && (
                        <Card>
                            <CardHeader>
                                <CardTitle>Topic Generator</CardTitle>
                                <CardDescription>
                                    {mode === 'extemp' 
                                        ? 'Generate three current event questions. Choose one to start your 30-minute prep time.'
                                        : 'Generate three prompts. Choose one and use your adjustable prep time before speaking.'}
                                </CardDescription>
                            </CardHeader>
                            <CardContent className="space-y-4">
                                <div className="flex gap-2">
                                    <Button onClick={handleGenerateTopics} className="w-full" disabled={isLoadingTopics}>
                                        {isLoadingTopics ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                                        Generate Topics
                                    </Button>
                                    <Button variant="outline" size="icon" onClick={handleGenerateTopics} disabled={isLoadingTopics}><RefreshCw className="h-4 w-4" /></Button>
                                </div>
                                
                                {topics.length > 0 && (
                                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                                        {topics.map(renderTopic)}
                                    </div>
                                )}
                            </CardContent>
                        </Card>
                    )}

                    {viewingArchived && selectedTopic && (
                         <Card>
                            <CardHeader>
                                <CardTitle>Archived: {selectedTopic.text}</CardTitle>
                                {viewedSpeech && <CardDescription>Practice session from {format(new Date(viewedSpeech.date), 'PPP')}</CardDescription>}
                            </CardHeader>
                        </Card>
                    )}

                    <Card>
                        <CardHeader className="flex flex-row items-center justify-between">
                            <CardTitle>Speech</CardTitle>
                            <div className="flex gap-2">
                                {isRecording ? (
                                    <Button variant="destructive" onClick={stopRecording}>
                                        <StopCircle className="mr-2"/> Stop Recording
                                    </Button>
                                ) : (
                                    <Button 
                                        onClick={startRecording} 
                                        disabled={!selectedTopic || !hasCameraPermission || !!videoUrl || viewingArchived}
                                    >
                                        <Video className="mr-2" /> Record Speech
                                    </Button>
                                )}
                                {videoUrl && !isRecording && (
                                     <Button onClick={handleSaveSpeech} disabled={viewingArchived}>
                                        <Save className="mr-2"/> Save Speech
                                    </Button>
                                )}
                            </div>
                        </CardHeader>
                        <CardContent>
                           <div className="aspect-video w-full bg-black rounded-md flex items-center justify-center relative">
                             <video ref={videoRef} src={videoUrl ?? undefined} className={cn("w-full h-full rounded-md", !videoUrl && "object-cover")} autoPlay={true} playsInline controls={!!videoUrl} muted={isRecording || (!videoUrl && !viewingArchived)} />
                             {!hasCameraPermission && (
                                <div className="absolute inset-0 flex items-center justify-center bg-black/50">
                                    <Alert variant="destructive" className="max-w-sm">
                                        <AlertTitle>Camera Access Required</AlertTitle>
                                        <AlertDescription>Please allow camera access in your browser to use this feature.</AlertDescription>
                                    </Alert>
                                </div>
                             )}
                           </div>
                        </CardContent>
                    </Card>

                    <Card>
                        <CardHeader>
                            <CardTitle>Notes</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <Textarea 
                                placeholder={viewingArchived ? "No notes were taken for this speech." : "Start typing your notes here..."}
                                className="min-h-[300px] text-base"
                                value={notes}
                                onChange={(e) => setNotes(e.target.value)}
                                readOnly={viewingArchived}
                            />
                        </CardContent>
                    </Card>
                </div>
                <div className="lg:col-span-1 space-y-6">
                    <Card>
                        <CardHeader>
                            <CardTitle>Prep Timer</CardTitle>
                        </CardHeader>
                        <CardContent className="flex flex-col items-center gap-4">
                             <div className="text-6xl font-bold font-mono tabular-nums">{formatTime(prepTime)}</div>
                            <div className="flex gap-2">
                                <Button size="icon" onClick={prepTimerActive ? pausePrepTimer : startPrepTimer} disabled={prepTime <= 0 || viewingArchived}>
                                    {prepTimerActive ? <Pause /> : <Play />}
                                </Button>
                            </div>
                            {mode === 'impromptu' && (
                                <div className="w-full space-y-2 pt-4">
                                    <label className="text-sm font-medium">Adjust Prep Time</label>
                                    <Slider
                                        min={0}
                                        max={7.5}
                                        step={0.5}
                                        value={[prepTime/60]}
                                        onValueChange={handleImpromptuSlider}
                                        disabled={prepTimerActive || speechTimerActive || viewingArchived}
                                    />
                                </div>
                            )}
                        </CardContent>
                    </Card>
                    <Card>
                        <CardHeader>
                            <CardTitle>Speech Timer</CardTitle>
                        </CardHeader>
                        <CardContent className="flex flex-col items-center gap-4">
                            <div className="text-6xl font-bold font-mono tabular-nums">{formatTime(speechTime)}</div>
                             <div className="flex gap-2">
                                <Button size="icon" onClick={speechTimerActive ? pauseSpeechTimer : startSpeechTimer} disabled={speechTime <= 0 || viewingArchived || isRecording}>
                                    {speechTimerActive ? <Pause /> : <Play />}
                                </Button>
                            </div>
                        </CardContent>
                    </Card>
                    <Card>
                        <CardHeader>
                            <CardTitle>Saved Speeches</CardTitle>
                        </CardHeader>
                        <CardContent>
                           {savedSpeeches.length > 0 ? (
                             <Accordion type="single" collapsible className="w-full">
                                {savedSpeeches.map(speech => (
                                     <AccordionItem value={speech.id} key={speech.id}>
                                        <AccordionTrigger className="text-sm text-left">{speech.topic}</AccordionTrigger>
                                        <AccordionContent>
                                            <p className="text-xs text-muted-foreground mb-2">{format(new Date(speech.date), 'PPP')}</p>
                                            <div className="flex gap-2">
                                                <Button size="sm" variant="secondary" className="flex-1" onClick={() => handleViewArchivedSpeech(speech)}>
                                                    <ChevronsRight className="mr-2 h-4 w-4"/> View
                                                </Button>
                                                <Button size="sm" variant="destructive" onClick={() => handleDeleteSpeech(speech.id)}>
                                                    <Trash2 className="mr-2 h-4 w-4"/> Delete
                                                </Button>
                                            </div>
                                        </AccordionContent>
                                     </AccordionItem>
                                ))}
                             </Accordion>
                           ) : (
                            <div className="text-center text-sm text-muted-foreground py-4">
                                No saved speeches yet.
                            </div>
                           )}
                        </CardContent>
                    </Card>
                </div>
            </div>

        </div>
    );
}


    